import { PerfilPageModule } from '../pages/perfil/perfil.module';
import { MispedidosPageModule } from '../pages/mispedidos/mispedidos.module';
import { KeyboardPage } from '../pages/keyboard/keyboard';
import { MousePage } from '../pages/mouse/mouse';
import { MousePageModule } from '../pages/mouse/mouse.module';
import { KeyboardPageModule } from '../pages/keyboard/keyboard.module';
import { AuricularesPage } from '../pages/auriculares/auriculares';
import { AuricularesPageModule } from '../pages/auriculares/auriculares.module';
import { OfertaPageModule } from '../pages/oferta/oferta.module';
//import { ListenApiProvider } from '../providers/server-api/server-api':
import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { Toast } from '@ionic-native/toast';
import { OfertaPage } from '../pages/oferta/oferta';



@NgModule({
  declarations: [
    MyApp,
    HomePage

    
  ],
  imports: [
    BrowserModule,
    ForgotpassPageModule,
    SignupPageModule,
    TransportistaPageModule,
    VendedorPageModule,
    LoginPageModule,
    PerfilPageModule,
    MousePageModule,
    OfertaPageModule,
    KeyboardPageModule,
    MispedidosPageModule,
    AuricularesPageModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/auriculares/auriculares.module.ngfactory#AuricularesPageModuleNgFactory', name: 'AuricularesPage', segment: 'auriculares', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/empty/empty.module.ngfactory#EmptyPageModuleNgFactory', name: 'EmptyPage', segment: 'empty', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/forgotpass/forgotpass.module.ngfactory#ForgotpassPageModuleNgFactory', name: 'ForgotpassPage', segment: 'forgotpass', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/keyboard/keyboard.module.ngfactory#KeyboardPageModuleNgFactory', name: 'KeyboardPage', segment: 'keyboard', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mispedidos/mispedidos.module.ngfactory#MispedidosPageModuleNgFactory', name: 'MispedidosPage', segment: 'mispedidos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mouse/mouse.module.ngfactory#MousePageModuleNgFactory', name: 'MousePage', segment: 'mouse', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/oferta/oferta.module.ngfactory#OfertaPageModuleNgFactory', name: 'OfertaPage', segment: 'oferta', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/perfil/perfil.module.ngfactory#PerfilPageModuleNgFactory', name: 'PerfilPage', segment: 'perfil', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signup/signup.module.ngfactory#SignupPageModuleNgFactory', name: 'SignupPage', segment: 'signup', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/transportista/transportista.module.ngfactory#TransportistaPageModuleNgFactory', name: 'TransportistaPage', segment: 'transportista', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/vendedor/vendedor.module.ngfactory#VendedorPageModuleNgFactory', name: 'VendedorPage', segment: 'vendedor', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LoginPage,
    MousePage,
    KeyboardPage,
    TransportistaPage,
    VendedorPage,
    OfertaPage,
    AuricularesPage,
    SignupPage,
    ForgotpassPage,
    PerfilPage,
    MispedidosPage
   
  ],
  providers: [
    StatusBar,
    SplashScreen,
    ApiClientService,
    BarcodeScanner,
    Toast,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



